
import ViewBox from 'vux/src/components/view-box/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import Tabbar from "@/components/Tabbar";
export default {
name: "sales",
components: {
ViewBox,
Tabbar,
Cell,
Group,
Card,
Countup,
Badge
},
created() {
this.queryUser();
this.queryShipOrder();
},
data() {
return {
userInfo: '',
badgeNum: 0
};
},
methods: {
queryUser() {
this.$api
.queryUser({})
.then(data => {
if (data.code === 200) {
this.userInfo = data.data.user;
localStorage.setItem("uid", data.data.user.uid);
} else {
this.$vux.toast.text(data.message, "top");
}
});
},
queryShipOrder() {



this.$api
.queryShipOrder({
pageNo: 1,
pageSize: 1000
})
.then(data => {
if (data.code === 200) {
this.badgeNum = data.data.items.length;
}

});
},
login() {
this.$api
.login({
username: 'oak',
password: 123456
})
.then(data => {
if (data.code === 200) {

} else {
this.$vux.toast.text(data.message, "top");
}
});
},
}
};



